import React from 'react';
//import React, { component } from 'react';

class Form extends React.Component {

constructor(props){
	super(props);
	
	this.state = { fullname: "Shiv Shankar Kumar",
					email: "shivshankar038@gmail.com",
					phone: "9555932566",
					message: "Web/UI Designer and Developer"
	}
}

handlechangeall = (event) => {
	this.setState({ [event.target.name] :event.target.value })
}

handlesubmit = (event) => {
	alert( JSON.stringify(this.state));
	event.preventDefault();
}

render(){
	return(
		<form onSubmit = { this.handlesubmit } >
			<label className="label"> Fullname </label>
			<input type="text" name="fullname" value={this.state.fullname}
				onChange={this.handlechangeall} /> <br />
			
			<label className="label"> Email </label>
			<input type="email" name="email" value={this.state.email}
				onChange={this.handlechangeall} /> <br />
			
			<label className="label"> Mobile </label>
			<input type="number" name="phone" value={this.state.phone}
				onChange={this.handlechangeall} /> <br />
			
			<label className="label"> Message </label>
			<textarea name="message" value={this.state.message}
				onChange={this.handlechangeall} /> <br />
			
			<input type="submit"  value="Send" />
		</form>
	)
}

}

export default Form;


