import React from 'react';
import './Signup.css';

class Signup extends React.Component {

constructor(props){
	super(props);
	
	this.state = { fullname: "Shiv Shankar Kumar",
					SignupEmail: "demo@gmail.com",
					SignupPhone: "9555932566",
					SignupPassward: "123456"
	}
}

handlechangeSignup = (event) => {
	this.setState({ [event.target.name] :event.target.value })
}

handleSignup = (event) => {
	alert( JSON.stringify(this.state));
	event.preventDefault();
}

render(){
	return(
		<div id="form">
			<h2> Create an account </h2>
			<form onSubmit = { this.handleSignup } >
				<label className="label"> Fullname </label>
				<input type="text" name="fullname" value={this.state.fullname}
					onChange={this.handlechangeSignup} /> <br />
				
				<label className="label"> Email </label>
				<input type="email" name="SignupEmail" value={this.state.SignupEmail}
					onChange={this.handlechangeSignup} /> <br />
				
				<label className="label"> Mobile </label>
				<input type="number" name="SignupPhone" value={this.state.SignupPhone}
					onChange={this.handlechangeSignup} /> <br />
				
				<label className="label"> Password </label>
				<input type="password" name="SignupPassward" value={this.state.SignupPassward}
					onChange={this.handlechangeSignup} /> <br />
				
				<input type="submit" name="Signup"  value="Sign Up" />
			</form>
		</div>
	)
}

}

export default Signup;


