import React from 'react';
import './Menustyle.css';
import '../images/logo.png';
import '../images/menu-icon.png';
import {Link} from 'react-router-dom';

const Menu = () => {
	return (
		<div id="menustyle">
			<div id="logo"><Link to="/"><img src={require('../images/logo.png')} alt='logo.png' /></Link></div>
			<div id="nav_toggle"><img src={require('../images/menu-icon.png')} alt='menu-icon.png' /></div>
			
				<ul id="navbar">
					<li> <Link to="Home" > Home </Link> </li>
					<li> <Link to="About" > About </Link> </li>
					<li> <Link to="Contact" > Contact </Link> </li>
				</ul>
		
		</div>
	)
}

export default Menu;


